import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
 import {
   faTwitter,
   faGoogle,
   faGithub,
 } from "@fortawesome/free-brands-svg-icons";
import { authService, firebaseInstance } from "fbase";
import AuthForm from "components/AuthForm";

const Auth = () => {
    const onSocialClick = async (event) => {
      const{
          target : {name},
      } = event;
      let provider;
      if(name === "google"){
          provider = new firebaseInstance.auth.GoogleAuthProvider();
      }else if(name === "github"){
          provider = new firebaseInstance.auth.GithubAuthProvider();
      }
      const data = await authService.signInWithPopup(provider);
    };

    return (
        <div className="authContainer">
            {/* <FontAwesomeIcon 
                icon={faTwitter}
                color={"#cb7575"}
                size="3x"
                style={{marginBottom: 30}}
            /> */}
            <img id="login_icon" src="https://dw-iconusers.flaticon.com/50935/50935311/1631264614554.svg?token=exp=1631266548~hmac=b8ad546b7266f168f99aaa130f1bd752" />
            <AuthForm />
            <div className="authBtns">
                <button onClick = {onSocialClick} name = "google" className="authBtn">Continue with google <FontAwesomeIcon icon={faGoogle} /></button>
                <button onClick = {onSocialClick} name = "github" className="authBtn">Continue with Github<FontAwesomeIcon icon={faGithub} /></button>
            </div>
        </div>
    );
};
export default Auth;